export default {
	name: 'loginModule',
	props:{
		btnText:{
			type: String,
			default: '登录'
		},
		loginTitle:{
			type: String,
			default: '登录'
		},
		imageSrc:{
			type: String,
			default: '../../static/authontication.png'
		},
		studengNumber:{
			type: Object,
			default: {'number':'学号',"placeHolder":"请输入学号"}
		},
		studengPassword:{
			type: Object,
			default: {'password':'密码',"placeHolder":"请输入密码"}
		}
	},
	data() {
		return {
			stuNum: '',
			password: ''
		}
	},
	methods: {
		inputNum(e){
			this.stuNum = e.detail.value;
			console.log(e);
		},
		inputPasswd(e){
			this.password = e.detail.value;
		},
		login(){
			if(this.stuNum || this.password){
				uni.showModal({
					content: '是否需要将图书馆密码保存到手机中，方便下次查询',
					cancelText: '不保存',
					confirmText: '保存'
				})
			
			}else{
				uni.showModal({
					title: '信息不全',
					content: '请填写完整信息',
					showCancel: true
				})
			}
			this.stuNum = this.password = '';
		}
	}
}
